.add-spell-icon {
  height: '1rem';
  width: '1rem';
}
.spell-results {
  border-top: 1px solid lightgray;
  margin-top: 2%;
}

.remove-spell-icon {
  cursor: pointer;
}

.no-components {
  font-style: italic;
  color: #888;
}
.footer {
  border-top: 1px solid lightgray;
  font-size: 0.75rem;
  margin-top: 2%;
}
